import { createStore } from 'vuex'
import axios from 'axios'

export default createStore({
  state: {
    serverURL: "https://dev1.med-game.ru/api/v1",
    testId: 11,
    questionId: 563,
    parentId: 0,
    infoText: "",
    questionText: "",
    answerTransitions: [],
    currentTransitionId: 0,
  },
  getters: {
    questionId(state){
      return state.questionId
    },
    bodyData(state){
      return state.bodyData
    },
    parentId(state){
      return state.parentId
    },
    infoText(state){
      return state.infoText
    },
    questionText(state){
      return state.questionText
    },
    answerTransitions(state){
      return state.answerTransitions
    },
    

  },
  mutations: {
    setDataQuestion(state, data){
      state.parentId = data.body.parentId
      state.infoText = data.body.infoText
      state.questionText = data.body.questionText
      state.answerTransitions = data.body.answerTransitions
      if (data.body.answerTransitions.length === 1) {
        state.currentTransitionId = data.body.answerTransitions[0].transitionId
      }
      else{
        state.currentTransitionId = 0
      }
    },
    setQuestionId(state, questionId){
      state.questionId = questionId
    },
    setCurrentTransitionId(state, transitionId){
      state.currentTransitionId = transitionId
    }
  },

  actions: {

    GET_QUESTION_DATA({state, commit}){
      axios({
        method: 'get',
        // url: `${state.serverURL}/admin/question/${state.questionId}`,
        // headers: { Authorization: `Bearer ${state.token}` }
        url: `${state.serverURL}/question/${state.questionId}`,
      })
      .then((questionData) => {
        commit("setDataQuestion", questionData.data.data)
        // console.log(questionData);
      })        
      .catch((error) => {
        console.log('Ошибка:');
        console.log(error)
        return error
      })
    },

    nextQuestion({commit, dispatch, state}){
      if (state.currentTransitionId) {
        commit('setQuestionId', state.currentTransitionId)
        return dispatch('GET_QUESTION_DATA')
      }
      else {
        console.log("Не выбран ответ")
      }
    },

    previousQuestion({commit, dispatch, state}){
      if (state.parentId) {
        commit('setQuestionId', state.parentId)
        return dispatch('GET_QUESTION_DATA')
      }      
    },

    setCurrentTransitionId({commit}, transitionId){
      commit("setCurrentTransitionId", transitionId)
    }

  },
  modules: {

  }
})
